.App {
  width: 100%;
  height: 100%;

  -webkit-user-select: text;
  user-select: text;
  -moz-user-select: text !important;
  -ms-user-select: text !important;

  & * {
    -webkit-user-select: text;
    user-select: text;
    -moz-user-select: text !important;
    -ms-user-select: text !important;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

#root {
  width: 100%;
  height: 100%;
}
